.agency-list {
  margin-top: 45px;

  .section_head {
    .title {
      font-size: 22px;
    }
  }

  
  .item {
    margin-inline: 0 !important;
    width: 100% !important;
    margin-bottom: 35px !important;

    .content {
      .title {
        position: relative;
        font-size: 16px;
        margin-bottom: 90px;

        &::after {
          content: "";
          height: 30px;
          width: 3px;
          background: $red3;
          position: absolute;
          right: -22px;
          top: -5px;
          bottom: 50%;
          transition: $transition;
        }
      }

      .data {
        font-weight: 200;
        font-size: 12px;
        top: -32px;
      }
    }
  }

  @media screen and (max-width:991px) {
    margin-bottom: 130px;
  }

  @media screen and (max-width:767px) {
    .item {
      .content {
        .title {
          margin-top: 15px;
          margin-bottom: 70px !important;
        }
      }
    }
  }
}